"use client";

import {
  motion,
  useMotionValue,
  useSpring,
  useTransform,
  AnimatePresence,
} from "framer-motion";
import {
  VscBook,
  VscBriefcase,
  VscProject,
  VscQuote,
  VscMail,
} from "react-icons/vsc";
import { usePathname, useRouter } from "next/navigation";
import { useMemo, useRef, useEffect } from "react";

const navItems = [
  { label: "About", icon: <VscBook size={20} />, id: "about" },
  { label: "Services", icon: <VscBriefcase size={20} />, id: "services" },
  { label: "Portfolio", icon: <VscProject size={20} />, id: "portfolio" },
  { label: "Testimonials", icon: <VscQuote size={20} />, id: "testimonials" },
  { label: "Contact", icon: <VscMail size={20} />, id: "contact" },
];

const springConfig = { mass: 0.1, stiffness: 150, damping: 12 };

export default function DockNavbar() {
  const mouseX = useMotionValue(Infinity);
  const isHovered = useMotionValue(0);
  const router = useRouter();
  const pathname = usePathname();

  const height = useSpring(
    useTransform(isHovered, [0, 1], [56, 100]),
    springConfig
  );

  const handleNav = (id: string) => {
    const el = document.getElementById(id);
    if (el) {
      el.scrollIntoView({ behavior: "smooth" });
    } else {
      router.push(`/#${id}`);
    }
  };

  return (
    <motion.div
      style={{ height }}
      className="fixed top-0 left-1/2 -translate-x-1/2 z-50 flex items-center justify-center pointer-events-none"
    >
      <motion.div
        onMouseMove={(e) => {
          isHovered.set(1);
          mouseX.set(e.pageX);
        }}
        onMouseLeave={() => {
          isHovered.set(0);
          mouseX.set(Infinity);
        }}
        className="bg-[#060010]/90 border border-neutral-700 backdrop-blur-lg rounded-2xl px-6 py-2 flex gap-4 pointer-events-auto shadow-xl"
      >
        {navItems.map((item, index) => (
          <DockButton
            key={item.id}
            icon={item.icon}
            label={item.label}
            onClick={() => handleNav(item.id)}
            mouseX={mouseX}
          />
        ))}
      </motion.div>
    </motion.div>
  );
}

function DockButton({
  icon,
  label,
  onClick,
  mouseX,
}: {
  icon: React.ReactNode;
  label: string;
  onClick: () => void;
  mouseX: any;
}) {
  const ref = useRef<HTMLDivElement>(null);
  const base = 44;
  const magnify = 70;
  const distance = 150;

 const dist = useTransform(mouseX, (val: number) => {
  const rect = ref.current?.getBoundingClientRect();
  return rect ? val - rect.x - rect.width / 2 : 0;
});


  const targetSize = useTransform(dist, [-distance, 0, distance], [base, magnify, base]);
  const size = useSpring(targetSize, springConfig);

  const isHovered = useMotionValue(0);

  useEffect(() => {
    const unsub = isHovered.on("change", (v) => {});
    return () => unsub();
  }, []);

  return (
    <motion.div
      ref={ref}
      style={{ width: size, height: size }}
      className="relative flex items-center justify-center rounded-full bg-[#060010] border border-neutral-600 cursor-pointer shadow-lg"
      onMouseEnter={() => isHovered.set(1)}
      onMouseLeave={() => isHovered.set(0)}
      onClick={onClick}
    >
      <div className="text-white">{icon}</div>

      {/* Tooltip below */}
      <AnimatePresence>
        {isHovered.get() === 1 && (
          <motion.div
            initial={{ opacity: 0, y: 5 }}
            animate={{ opacity: 1, y: 10 }}
            exit={{ opacity: 0, y: 5 }}
            transition={{ duration: 0.2 }}
            className="absolute top-full mt-2 px-2 py-1 bg-black text-white text-xs rounded shadow-md"
          >
            {label}
          </motion.div>
        )}
      </AnimatePresence>
    </motion.div>
  );
}
